.search-panel {
  margin: 1rem 0;
}

.search-panel .search-input {
  width: auto;
  flex-grow: 1;
  margin-right: 3px;
}

@media (max-width: 320px){
  .search-panel {
    margin: 1rem 0;
    width: 320px;
  }
  .search-panel .search-input {
    width: 0;
    flex-grow: 1;
    margin-right: 3px;
  }
}